import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faTwitter } from '@fortawesome/free-brands-svg-icons'
import { faInstagram } from '@fortawesome/free-brands-svg-icons'
import React from 'react'

const About = () => {
    return (
        <div className="font-title text-4xl container text-center mx-auto h-full mb-auto">
            <h1><a target="__blank" href="https://twitter.com/stallout_art"><FontAwesomeIcon icon={faTwitter} /></a> <a target="__blank" href="https://instagram.com/stallout.art"><FontAwesomeIcon icon={faInstagram} /></a></h1>
        </div>
    )
}

export default About
